@import "config/index";
@import "utilities/index";
@import "base/index";
@import "global";

@import "components/page-footer";
@import "components/page-frame";
@import "components/page-header";

@import "code/index";
@import "elements/index";
@import "bilingual.css";
@import "impressum";
@import "disclaimer";

#main
{
    width: auto;
    margin-left: 0;
    margin-right: 0;
}

.na{
  color:#999;
  font-style: italic;
}

.searchPage h1 {
    font-size: 1rem;
    padding:10px 0 18px;
}

.searchResultItem {
    padding: 0 0 32px;
}

.searchResultItem ul {
    margin: 0;
}

.searchResultItem .content {
    line-height: 130%;
}

.searchResultItem .content div {
    font-size:0.875rem;
    margin:4px 0 0 0;
}

.searchResultItem .date {
    color:#666;
}

.searchResultItem li {
    display: inline-block;
    font-size:0.875rem;    
}

.searchResultItem li a {
    color:#666;
}

.searchResultItem li a:hover {
    color:#007bb8;
}

.searchResultItem li::after {
    content: '\\';
    vertical-align: baseline;
    padding: 0 6px;
    color: #ababab;
}

.theme_night .searchResultItem li:after {
    color: #6e6e6e
}

.searchResultItem li:last-child {
    color: black;
}

.theme_night .searchResultItem li:last-child {
    color: #828282;
}

.searchResultItem li:last-child:before,.searchResultItem li:last-child:after {
    content: ''
}

.searchPaging
{
    float:right;
    position:relative;
    left:-50%;
    text-align:left;
    margin:48px 0;
}

.searchPaging ul
{
    list-style:none;
    position:relative;
    left:50%;
}

.searchPaging li
{
    float:left;
    position:relative;
}

.searchPaging li a {
    border:solid 2px #fff;
    width: 40px;
    height: 40px;
    text-align: center;
    display:block;
    line-height:210%;    
}

.theme_night .searchPaging li a {
    border:solid 2px #1a1a1a;
}

.searchPaging li a:hover {
    background-color:#ededed;
    text-decoration: none;
}

.theme_night .searchPaging li a:hover {
    background-color:#444;
}

.searchPaging li a.selected {
    border-color:#999;
}

.searchPaging li a.icon {
    font-family: 'icons' !important;
    font-size: 1.4em !important;
    line-height: 160% !important;
    color:#666;
}

.searchPaging li a.icon:hover {
    color:#0070a7;
}

@media only screen and (max-width: 600px) {
    .searchPage h1 {
        padding: 0 0 6px;
    }

    .searchPaging
    {
        margin:32px 0;
    }

    .searchPaging li.edge {
        display:none
    }
}